/***************************************
Aione Content
***************************************/
.aione-contenttop .widgettitle,
.aione-contentbottom .widgettitle{
	display: none
}
.aione-main .aione-page-content{ 
	width: 100%;
	float:none;
	box-sizing: border-box;
	margin-bottom: 20px;
}
@media only screen and (min-width: 993px){
	.aione-wrapper.sidebar-left .aione-main .aione-page-content,
	.aione-wrapper.sidebar-right .aione-main .aione-page-content{ 
		width: 70%;
		float:left;
		margin-bottom: 0;
	}

	.aione-wrapper.sidebar-left .aione-main .aione-page-content{
		padding-left: 30px;
	}
	.aione-wrapper.sidebar-right .aione-main .aione-page-content{ 
		padding-right: 30px;
	}
	.aione-wrapper.sidebar-left.sidebar-right .aione-main .aione-page-content{
		width: 50%; 
	}
}