/***************************************
Aione Footer
***************************************/
.aione-footer{
	color: #b4b4b4;
	margin:0;
	padding: 10px 0px;
	font-size: 1em;
	line-height: 1.5;
	font-weight: normal;
	text-align:center;
    background-color:#222a2d; 
}
.aione-footer.sticky{
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 101;
}
.aione-footer .aione-row{
	padding:0;
}
.aione-footer a, 
.aione-footer a:visited{
	text-decoration: none;
	webkit-transition: all 150ms ease-out;
	-moz-transition: all 150ms ease-out;
	-o-transition: all 150ms ease-out;
	transition: all 150ms ease-out;
}
.aione-footer.light a,
.aione-footer.light a:visited{
	color: #666666;
}
.aione-footer.light a:hover,
.aione-footer.light a:active{
	color: #454545;
}
.aione-footer.dark a,
.aione-footer.dark a:visited{
	color: #f2f2f2;
}
.aione-footer.dark a:hover,
.aione-footer.dark a:active{
	color: #e8e8e8;
}



/************************************************
Common Widget Style in footer
************************************************/
.aione-footer .widget h3{
	text-align:left;
	color:#ffffff;
	font-size: 20px;
	border-bottom: 2px solid #b2b2b2;
	padding-bottom: 15px;
}
.aione-footer .widget_nav_menu ul li{
	line-height: 40px;
	border-bottom: 1px solid #353535;
	text-align: left;
}
.aione-footer .widget_nav_menu ul li a{
	color: #ffffff;
	display: block;
	font-size: 16px;
    
}




/*********[   Categories Widget   ]***************/
.aione-footer .widget_categories ul li{
	text-align: right;
}
.aione-footer .widget_categories ul li a{
	float: left;
}

/*********[   Text Widget   ]***************/

.aione-footer .textwidget{
	color: #fff;
    line-height: 25px;
    letter-spacing: 1.1px;
}
.aione-footer .textwidget p{
	text-align:justify;
}

/*********[   Recent Post Widget   ]***************/

.aione-footer .widget_recent_entries ul > li{
	padding: 10px 0;
}
.aione-footer .widget_recent_entries ul > li > a{
	font-size: 16px;
	font-weight: 600;
	line-height: 1.6;
	
}
.aione-footer .widget_recent_entries ul > li > span{
	color: #999;
	font-size:13px;
	display: block;
	line-height: 1.6;
	margin-top: 6px;
}

/*********[   Social Icons Widget   ]***************/

.aione-footer .widget.aione-social-icons-widget ul li{
	
}
.aione-footer .widget.aione-social-icons-widget ul li a{
	text-align: center;
}



/*********[   Gallery Widget   ]***************/

.aione-footer .gallery .gallery-icon.landscape > a{
	display: inline-block;
	padding: 0 10px 10px 0;
}
.aione-footer .gallery-item .gallery-icon.landscape img{
	width: -webkit-fill-available;
}

/*********[   Calender Widget   ]***************/
.aione-footer .calendar_wrap thead tr th,
.aione-footer .calendar_wrap caption,
.aione-footer .calendar_wrap tbody tr td{
	line-height: 40px;
    width: 40px;
    text-align: center;
    color: white
}
.aione-footer .calendar_wrap thead{
	border-bottom:1px solid #e8e8e8;
}
.aione-footer .calendar_wrap table {
	margin: 0 auto;
    border: 1px solid #e8e8e8;
}
.aione-footer .calendar_wrap caption{
	width: auto;
}

/*********[   Navigation Widget   ]***************/
.aione-footer .widget_nav_menu .sub-menu{
	background-color: rgba(232, 232, 232, 0.1);
	display: none;
}
.aione-footer .widget_nav_menu .menu-item:hover .sub-menu{
	display: block;
}
.aione-footer .widget_nav_menu .sub-menu li > a{
	padding: 5px 10px; 
}
.aione-footer .widget_nav_menu .sub-menu li > a:hover{
	background-color: #212121;
}

