/***************************************
LAYOUT
***************************************/
.aione-wrapper{
	margin: 0;
    padding: 0;
}
.aione-layout-wide{
	width:100%;
}
.aione-main{
	
}
.aione-wrapper.no-header .aione-main{
	padding-top: 0px;
}

.aione-layout-boxed .wrapper{
	max-width:1600px;
	margin-left:auto;
	margin-right:auto;
}
.aione-layout-wide .aione-header .wrapper,
.aione-layout-wide .aione-main .wrapper,
.aione-layout-wide .aione-footer .wrapper,
.aione-layout-wide .aione-copyright .wrapper{
	max-width:1600px;
	margin-left:auto;
	margin-right:auto;
}



.aione-wrapper > .wrapper{
	width: 100%;
	margin: 0 auto;
}
.aione-layout-wide .aione-topbar > .wrapper,
.aione-layout-wide .aione-header > .wrapper,
.aione-layout-wide .primary-nav > .wrapper,
.aione-layout-wide .aione-slider > .wrapper,
.aione-layout-wide .aione-pagetitle > .wrapper,
.aione-layout-wide .aione-pagetop > .wrapper,
.aione-layout-wide .aione-pagebottom > .wrapper,
.aione-layout-wide .aione-main > .wrapper,
.aione-layout-wide .aione-footer > .wrapper,
.aione-layout-wide .aione-copyright > .wrapper,
.aione-layout-wide .aione-powered-by > .wrapper,
.aione-layout-wide .aione-section > .wrapper{
	max-width: 1200px;
	margin: 0 auto;
}

.aione-layout-wide .aione-topbar.fullwidth > .wrapper,
.aione-layout-wide .aione-header.fullwidth > .wrapper,
.aione-layout-wide .primary-nav.fullwidth > .wrapper,
.aione-layout-wide .aione-slider.fullwidth > .wrapper,
.aione-layout-wide .aione-pagetitle.fullwidth > .wrapper,
.aione-layout-wide .aione-pagetop.fullwidth > .wrapper,
.aione-layout-wide .aione-pagebottom.fullwidth > .wrapper,
.aione-layout-wide .aione-main.fullwidth > .wrapper,
.aione-layout-wide .aione-footer.fullwidth > .wrapper,
.aione-layout-wide .aione-copyright.fullwidth > .wrapper,
.aione-layout-wide .aione-powered-by.fullwidth > .wrapper,
.aione-layout-wide .aione-section.fullwidth > .wrapper{
  max-width: 100%;
}

.aione-layout-boxed .aione-topbar > .wrapper,
.aione-layout-boxed .aione-header > .wrapper,
.aione-layout-boxed .primary-nav > .wrapper,
.aione-layout-boxed .aione-slider > .wrapper,
.aione-layout-boxed .aione-pagetitle > .wrapper,
.aione-layout-boxed .aione-pagetop > .wrapper,
.aione-layout-boxed .aione-pagebottom > .wrapper,
.aione-layout-boxed .aione-main > .wrapper,
.aione-layout-boxed .aione-footer > .wrapper,
.aione-layout-boxed .aione-copyright > .wrapper,
.aione-layout-boxed .aione-powered-by > .wrapper,
.aione-layout-boxed .aione-section > .wrapper{
	padding-left: 20px;
	padding-right: 20px;
}

.aione-layout-boxed .aione-topbar.fullwidth > .wrapper,
.aione-layout-boxed .aione-header.fullwidth > .wrapper,
.aione-layout-boxed .primary-nav.fullwidth > .wrapper,
.aione-layout-boxed .aione-slider.fullwidth > .wrapper,
.aione-layout-boxed .aione-pagetitle.fullwidth > .wrapper,
.aione-layout-boxed .aione-pagetop.fullwidth > .wrapper,
.aione-layout-boxed .aione-pagebottom.fullwidth > .wrapper,
.aione-layout-boxed .aione-main.fullwidth > .wrapper,
.aione-layout-boxed .aione-footer.fullwidth > .wrapper,
.aione-layout-boxed .aione-copyright.fullwidth > .wrapper,
.aione-layout-boxed .aione-powered-by.fullwidth > .wrapper,
.aione-layout-boxed .aione-section.fullwidth > .wrapper{
	padding-left: 0px;
	padding-right: 0px;
}
.aione-main > .wrapper{
	padding-top: 0px;
}
.aione-main.page-padding > .wrapper{
	padding-top: 30px;
}
.aione-page-content > .wrapper{
	padding-top: 0px;
}
.aione-main.page-content-padding .aione-page-content > .wrapper{
	padding-top: 30px;
}
.aione-main > .wrapper::after{
	content: '';
	display: table;
	clear: both;
}

@media #{$medium-and-up} {
    
}
@media #{$large-and-up} {
    .layout-header-left > .wrapper{
        display:flex;
    }
}