/***************************************
Aione Topbar
***************************************/

.aione-topbar{
	margin: 0;
	padding: 0;
	color:#b2b2b2;
	line-height: 36px;
	border-top: none;
	background-color: #23282d
}
.aione-topbar > .wrapper{
	padding: 2px 0;
	min-height: 40px;
	max-height: 120px;
	overflow:hidden; 
	line-height: inherit;
	box-sizing: border-box;
}
.aione-topbar,
.aione-topbar a{
	color:#b2b2b2;
	font-size: 1em;
	font-weight: 400;
	line-height: 36px;
	line-height: inherit;
}
.aione-topbar a:hover{
	color:#ffffff;
}
.aione-topbar p{
	font-size: 1em;
	margin-bottom:0;
	line-height: inherit;
}

.aione-topbar-left{
	float:left;
	text-align: left;
	width: 50%;
}
.aione-topbar-right{
	float:right;
	text-align: right;
	width: 50%;	
}
.aione-topbar-center{
	float:none;
	text-align: center;	
}

.aione-topbar .aione-topbar-left .widget{
	margin-right: 8px;
}
.aione-topbar .aione-topbar-right .widget{
	margin-left: 8px;
}
.aione-topbar .widget .widgettitle{
	display: none;
}
/*.aione-topbar .widget{
  display: inline-block;
  margin-right: 15px;
}
.aione-topbar .widget:last-child{
	margin-right: 0px;
}*/
.aione-topbar .aione-topbar-left .widget{
  float: left;
}
.aione-topbar .aione-topbar-right .widget{
  float: right;
}


.aione-topbar .aione_social_icons_widget ul > li{
	display: inline-block;
	margin-right: 5px;
}
.aione-topbar .aione_social_icons_widget ul > li i{
	margin-right: 5px;
}

.aione-topbar .aione-social-icons{
	margin-bottom: 0;
}



/************************************************
Common Widget Style in Topbar
************************************************/

/*********[   Navigation Widget   ]***************/
.aione-topbar .widget_nav_menu .sub-menu{
	display: none;
	position: absolute;
    width: 300px;
    background: white;
    z-index: 99;
    box-shadow: 1px 1px 4px 1px #e8e8e8;
}
.aione-topbar .widget_nav_menu .sub-menu li,
.aione-topbar .widget_nav_menu .sub-menu li > a{
	display: block;
}
.aione-topbar .widget_nav_menu .sub-menu li > a{
	padding: 5px 10px; 
}
.aione-topbar .widget_nav_menu .sub-menu li > a:hover{
	background-color: #e8e8e8;
}
.aione-topbar .widget_nav_menu .menu-item:hover .sub-menu{
	display: block;
}
.aione-topbar .widget_nav_menu li{
	display: inline-block;
}
.aione-topbar .widget_nav_menu li:after{
	content:"|";
	padding:0 10px;
}
.aione-topbar .widget_nav_menu .sub-menu li:after,
.aione-topbar .widget_nav_menu li:last-child:after{
	content:"";
	padding:0;
}


/***********************************
User Links
***********************************/
.aione-topbar .user-links{
    display:inline-block;
}

.aione-topbar .user-links a{
    color: #368dc4;
    background-color: #ffffff;
    border-radius: 100px;
    padding: 4px 20px;
}

.aione-topbar  .user-welcome{ 
    margin-right: 10px;
    color: #fff; 
}


/*********[   Meta Widget   ]***************/
.aione-topbar .widget_meta ul > li{
	display: inline-block;
	padding: 0 10px;
}
