/***************************************
Aione Copyright
***************************************/
.aione-copyright {
    color: #acacac;
    margin: 0;
    padding: 0;
    font-size: 1.1em;
    line-height: 1.5;
    font-weight: normal;
    text-align: center;
    background-color:#1c2225;
}
.aione-copyright > .wrapper{
    padding:10px 0;
}
.aione-copyright a{
    color: #acacac;
    text-decoration: none;
    -webkit-transition: all 150ms ease-out;
    -moz-transition: all 150ms ease-out;
    -o-transition: all 150ms ease-out;
    transition: all 150ms ease-out;
}
.aione-copyright a:hover,
.aione-copyright a:active{
	color: #ffffff;
}
.aione-copyright.sticky{
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 102;
}
.aione-copyright-left{
    float:left;
    text-align: left;
}
.aione-copyright-right{
    float:right;
    text-align: right;  
}
.aione-copyright-center{
    float:none;
    text-align: center; 
}
.aione-copyright .widget .widgettitle{
    display: none;
}
.aione-copyright .widget_nav_menu li{
    display: inline-block;
}
.aione-copyright .widget_nav_menu li:after{
    content:"/";
    padding:0 10px;
}
.aione-copyright .widget_nav_menu li:last-child:after{
    content:"";
    padding:0;
}
.aione-copyright .aione-copyright-left .textwidget p, 
.aione-copyright .aione-copyright-right .textwidget p{
}
.aione-copyright .aione-social-icons-widget > ul{
    margin-bottom: 0;
}

.aione-copyright p{
    margin-bottom:0;
}