/***************************************
Aione Sidebar
***************************************/
.aione-sidebar:before{
    display:none;
}
.aione-sidebar {
    width: 100%;
    float:none;
    position: relative;
    margin-bottom: 20px;
}
@media only screen and (min-width: 993px){
    .aione-sidebar {
        width: 30%;
        position: relative;
        margin-bottom: 0;
    }
    .aione-wrapper.sidebar-left.sidebar-right .aione-sidebar{
        width: 25%;
    }
} 
/***************************************
Aione Sidebar Left
***************************************/
@media only screen and (min-width: 993px){
    .aione-sidebar-left {
        float:left;
    }
    .aione-main.sidebar-left .aione-sidebar-left > .wrapper{
        margin-right: 30px;
    }
}
/***************************************
Aione Sidebar Right
***************************************/
@media only screen and (min-width: 993px){
    .aione-sidebar-right {
        float:right;
    }
    .aione-main.sidebar-right .aione-sidebar-right > .wrapper{
        margin-left: 30px;
    }
}




/************************************************
Common Widget Style in sidebars
************************************************/

.aione-sidebar .widget{

}
.aione-sidebar .widgettitle{
    color: #263238;
    font-size: 17px;
}
.aione-sidebar .widget ul > li{
    padding: 10px 0;
    border-bottom: 1px solid #e8e8e8;
}
.aione-sidebar .widget ul > li:last-child{
    border-bottom: none;
}
.aione-sidebar .widget ul > li > a{
    display: block;
}

/*********[   Categories Widget   ]***************/

.aione-sidebar .widget_categories ul > li{
    text-align: right;
}
.aione-sidebar .widget_categories ul > li:after{
    content: '';
    display: block;
    clear: left;
}
.aione-sidebar .widget_categories ul > li > a{
    float: left;
    color: #757575;
}

/*********[   Recent Post Widget   ]***************/

.aione-sidebar .widget_recent_entries ul > li > a{
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 6px;
    color: #757575;
}
.aione-sidebar .widget_recent_entries ul > li > span{
    color: #999;
    font-size:13px;
}

/*********[   Social Icons Widget   ]***************/

.aione-sidebar .aione-social-icons-widget ul > li{
    padding: 0;
    border-bottom: none;
}

/*********[   Calender Widget   ]***************/

.aione-sidebar .calendar_wrap thead tr th,
.aione-sidebar .calendar_wrap caption,
.aione-sidebar .calendar_wrap tbody tr td{
    line-height: 40px;
    text-align: center;
}
.aione-sidebar .calendar_wrap thead{
    border-bottom:1px solid #e8e8e8;
}
.aione-sidebar .calendar_wrap table {
    margin: 0 auto;
    border: 1px solid #e8e8e8;
    width: 100%;
}
.aione-sidebar .calendar_wrap caption{
    width: auto;
}

/*********[   Meta and Archive Widget   ]***************/

.aione-sidebar .widget_meta  ul > li > a,
.aione-sidebar .widget_archive ul > li > a{
    color: #757575;
}

/*********[   Pages Widget   ]***************/

.aione-sidebar .widget_pages li > a{
    color: #757575;
}

/*********[   Gallery Widget   ]***************/

.aione-sidebar .gallery .gallery-icon.landscape > a{
    display: inline-block;
    padding: 0 10px 10px 0;
}
.aione-sidebar .gallery-item .gallery-icon.landscape img{
    width: -webkit-fill-available;
}

/*********[   Menu Widget   ]***************/
.aione-sidebar .widget_nav_menu .sub-menu{
    top: 0;
    z-index: 99;
    width: 300px;
    display: none;
    position: absolute;
    background-color: white;
    box-shadow: 1px 1px 4px 1px #e8e8e8;
}
.aione-sidebar.aione-sidebar-right .widget_nav_menu .sub-menu{
    right: 100%;
}
.aione-sidebar.aione-sidebar-left .widget_nav_menu .sub-menu{
    left: 100%;
}
.aione-sidebar .widget_nav_menu .sub-menu li,
.aione-sidebar .widget_nav_menu .sub-menu li > a{
    display: block;
}
.aione-sidebar .widget_nav_menu .sub-menu li > a{
    padding: 10px 5px; 
}
.aione-sidebar .widget_nav_menu .sub-menu li > a:hover{
    background-color: #e8e8e8;
}
.aione-sidebar .widget_nav_menu .menu-item:hover .sub-menu{
    display: block;
}
.aione-sidebar .widget_nav_menu ul > li{
    display:block;
    padding: 0;
    position: relative;
}
.aione-sidebar .widget_nav_menu ul > li > a{
    display:block;
    padding: 10px 0;
    font-size: 17px;
    color: #757575;
}


.aione-sidebar .aione_social_share_widget ul > li{
    padding: 0;
  border-bottom: none;
}





.aione-sidebar .widgettitle{
      font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    color: #333;
}